import { HttpClient, HttpParams } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { AuthenticationService } from '../authentication/authentication.service';
import { Asset, ExtendedAsset, IUpdateAsset } from './asset.types';

@Injectable({
  providedIn: 'root'
})
export class AssetService {
  private assetUrl = `${environment.apiBaseUrl}/asset`;

  constructor(
    private httpClient: HttpClient,
    private authenticationService: AuthenticationService
  ) {}

  private get primarySiteId(): string {
    const userProfile = this.authenticationService.currentUserProfile$.getValue();
    if (userProfile && userProfile.primarySite) {
      return userProfile.primarySite.id;
    }
    return null;
  }

  public listAssets(
    args: {
      offset: number;
      take: number;
      text: string;
    },
    siteId = this.primarySiteId
  ): Observable<Asset[]> {
    let params = new HttpParams();
    params = params.append('offset', args.offset.toString());
    params = params.append('take', args.take.toString());
    params = params.append('text', args.text.toString());
    params = params.append('siteId', siteId);

    return this.httpClient.get<Asset[]>(`${this.assetUrl}`, {
      params
    });
  }

  public viewAssetForId(Id: string): Observable<ExtendedAsset> {
    return this.httpClient.get<ExtendedAsset>(`${this.assetUrl}/${Id}`);
  }

  public updateAssetForId(
    updateAsset: IUpdateAsset
  ): Observable<ExtendedAsset> {
    return this.httpClient.put<ExtendedAsset>(`${this.assetUrl}`, updateAsset);
  }

  public getAssetGroup(text: string) {
    return this.httpClient.get(
      `${this.assetUrl}/AssetGroupAndSubGroups?Text=${text}`
    );
  }
}
