export enum AssetType {
  Critical = '2',
  Major = '3'
}

export enum Phase {
  Single = '1',
  Three = '3'
}

export enum IAssetStatus {
  Active = 0,
  Obsolete = 1
}

export interface IAsset {
  id: string;
  assetId: number;
  assetCode: string;
  name: string;
  description: string;
  statusId?: number;
  location?: string;
}

export class Asset {
  public id: string;
  public assetId: number;
  public assetCode: string;
  public name: string;
  public description: string;
  public statusId?: number;
  public location?: string;

  constructor(asset?: IAsset) {
    if (asset) {
      this.id = asset.id;
      this.assetId = asset.assetId;
      this.assetCode = asset.assetCode;
      this.name = asset.name;
      this.description = asset.description;
      this.statusId = asset.statusId;
      this.location = asset.location;
    }
  }
}

export interface IGroup {
  id: number;
  name: string;
}

export class Group {
  public id: number;
  public name: string;

  constructor(group?: IGroup) {
    if (group) {
      this.id = group.id;
      this.name = group.name;
    }
  }
}

export interface ISubGroup {
  id: number;
  name: string;
}

export class SubGroup {
  public id: number;
  public name: string;

  constructor(subGroup?: ISubGroup) {
    if (subGroup) {
      this.id = subGroup.id;
      this.name = subGroup.name;
    }
  }
}

export interface IAssetType {
  id: number;
  name: string;
}

export interface IExtendedAsset {
  id: string;
  assetId: number;
  assetCode: string;
  name: string;
  description: string;
  supplier?: string;
  location?: string;
  nextMaintenanceDate?: string;
  model?: string;
  manufacturer?: string;
  assetGroup?: IGroup;
  assetSubGroup?: ISubGroup;
  assetType?: IAssetType;
  kw?: number;
  volts?: number;
  phase?: string;
  loadFactor?: number;
  specifications?: string;
  vendorId?: string;
  frequencyDays?: number;
  validFromDate?: string;
  validToDate?: string;
  attachedFiles?: string[];
}

export class ExtendedAsset implements IExtendedAsset {
  public id: string;
  public assetId: number;
  public assetCode: string;
  public name: string;
  public description: string;
  public supplier: string;
  public location: string;
  public nextMaintenanceDate: string;
  public model: string;
  public manufacturer: string;
  public assetGroup: IGroup;
  public assetSubGroup: ISubGroup;
  public assetType: IAssetType;
  public kw: number;
  public volts: number;
  public phase: string;
  public loadFactor: number;
  public specifications: string;
  public vendorId: string;
  public frequencyDays: number;
  public validFromDate: string;
  public validToDate: string;
  public attachedFiles?: string[];

  constructor(iExtendedAsset?: IExtendedAsset) {
    if (iExtendedAsset) {
      this.id = iExtendedAsset.id;
      this.assetId = iExtendedAsset.assetId;
      this.assetCode = iExtendedAsset.assetCode;
      this.name = iExtendedAsset.name;
      this.description = iExtendedAsset.description;
      this.supplier = iExtendedAsset.supplier;
      this.location = iExtendedAsset.location;
      this.nextMaintenanceDate = iExtendedAsset.nextMaintenanceDate;
      this.model = iExtendedAsset.model;
      this.manufacturer = iExtendedAsset.manufacturer;
      this.assetGroup = iExtendedAsset.assetGroup;
      this.assetSubGroup = iExtendedAsset.assetSubGroup;
      this.assetType = iExtendedAsset.assetType;
      this.kw = iExtendedAsset.kw;
      this.volts = iExtendedAsset.volts;
      this.phase = iExtendedAsset.phase;
      this.loadFactor = iExtendedAsset.loadFactor;
      this.specifications = iExtendedAsset.specifications;
      this.vendorId = iExtendedAsset.vendorId;
      this.frequencyDays = iExtendedAsset.frequencyDays;
      this.validFromDate = iExtendedAsset.validFromDate;
      this.validToDate = iExtendedAsset.validToDate;
      this.attachedFiles = iExtendedAsset.attachedFiles;
    }
  }
}

export interface IUpdateAsset {
  id: string;
  model?: string;
  manufacturer?: string;
  assetGroupId?: number;
  assetSubGroupId?: number;
  assetTypeId?: number;
  kw?: number;
  volts?: number;
  phase?: string;
  loadFactor?: number;
  specifications?: string;
  vendorId?: string;
  frequencyDays?: number;
  validFromDate?: string;
  validToDate?: string;
}

export class AssetGroupAndSubGroup {
  public id: number;
  public name: string;
  public assetSubGroups: ISubGroup[];
}

export interface IFileCollection {
  src: string;
  caption: string;
  thumb: string;
  path?: string;
  file?: File;
  name: string;
}

export interface IAssets extends IAsset {
  referenceId: string;
  isDelete?: boolean;
  isNew?: boolean;
}
