import { HttpClient, HttpParams } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from 'src/environments/environment';
import { AuthenticationService } from '../authentication/authentication.service';
import { IUserProfile } from '../user-account/user';
import {
  IPreventiveMaintenance,
  PreventiveMaintenance,
  PreventiveMaintenanceStatus
} from './preventive-maintenance.types';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class PreventiveMaintenanceService {
  private preventiveMaintenanceUrl = `${environment.apiBaseUrl}/preventivemaintenance/scheduler`;

  constructor(
    private http: HttpClient,
    private authenticationService: AuthenticationService
  ) {}

  private get currentUserProfile(): IUserProfile {
    const userProfile = this.authenticationService.currentUserProfile$.getValue();
    if (userProfile && userProfile.primarySite) {
      return userProfile;
    }
    return null;
  }

  public getPreventiveMaintenanceList(
    args: {
      offset: number;
      take: number;
      searchText: string;
      includeExpired: boolean;
    },
    siteId = this.currentUserProfile.primarySite.id
  ): Observable<IPreventiveMaintenance[]> {
    let params = new HttpParams();
    params = params.append('offset', args.offset.toString());
    params = params.append('take', args.take.toString());
    params = params.append('searchText', args.searchText.toString());
    params = params.append(
      'includeExpiredSchedule',
      args.includeExpired.toString()
    );
    return this.http.get<IPreventiveMaintenance[]>(
      `${this.preventiveMaintenanceUrl}/schedules/${siteId}?`,
      { params }
    );
  }

  public createSchedule(args: {
    title: string;
    endDate: string;
    startDate: string;
    frequency: string;
    status: PreventiveMaintenanceStatus;
    assignedTo: string;
    instruction: string;
    assetName: string;
    maintenanceAssets?: string[];
    siteId?: string;
  }): Observable<PreventiveMaintenance> {
    const createdBy: string = this.authenticationService.getCurrentUserIdAndName();
    args.siteId = this.currentUserProfile.primarySite.id;
    const body = args;
    return this.http.post<PreventiveMaintenance>(
      `${this.preventiveMaintenanceUrl}?userId=${createdBy}`,
      body
    );
  }

  public viewPreventiveMaintenanceDetailForId(
    preventiveMaintenanceGuId: string
  ) {
    return this.http.get<IPreventiveMaintenance>(
      `${this.preventiveMaintenanceUrl}/${preventiveMaintenanceGuId}`
    );
  }

  public updatePreventiveMaintenanceDetailForId(
    preventiveMaintenanceGuId: string,
    extendedPreventiveMaintenance: IPreventiveMaintenance,
    userId = this.authenticationService.getCurrentUserIdAndName()
  ): Observable<IPreventiveMaintenance> {
    return this.http.put<IPreventiveMaintenance>(
      `${this.preventiveMaintenanceUrl}/${preventiveMaintenanceGuId}/${userId}`,
      extendedPreventiveMaintenance
    );
  }
}
