import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import {
  IWorkorder,
  IWorkOrderServiceDetail,
  IWorkOrderSpares
} from './workorder.types';
import { HttpClient, HttpParams } from '@angular/common/http';
import { AuthenticationService } from '../authentication/authentication.service';
import { environment } from 'src/environments/environment';
import { IExtendedWorkOrder } from './workorder.types';
import { IUserProfile } from '../user-account/user';

@Injectable({
  providedIn: 'root'
})
export class WorkordersService {
  private workOrderURL = `${environment.apiBaseUrl}/work-order`;

  constructor(
    private httpClient: HttpClient,
    private authenticationService: AuthenticationService
  ) {}

  private get currentUserProfile(): IUserProfile {
    const userProfile = this.authenticationService.currentUserProfile$.getValue();
    if (userProfile && userProfile.primarySite) {
      return userProfile;
    }
    return null;
  }

  public listWorkorders(
    args: {
      offset: number;
      take: number;
      text: string;
    },
    siteId = this.currentUserProfile.primarySite.id
  ): Observable<IWorkorder[]> {
    let params = new HttpParams();
    params = params.append('offset', args.offset.toString());
    params = params.append('take', args.take.toString());
    params = params.append('searchText', args.text.toString());

    return this.httpClient.get<IWorkorder[]>(
      `${this.workOrderURL}/${siteId}?`,
      { params }
    );
  }

  public viewWorkOrderForId(
    workOrderId: string,
    siteId = this.currentUserProfile.primarySite.id
  ) {
    return this.httpClient.get<IExtendedWorkOrder>(
      `${this.workOrderURL}/${siteId}/${workOrderId}`
    );
  }

  public updateWorkOrderForId(
    workOrderGuId: string,
    extendedWorkOrder: IExtendedWorkOrder,
    userId = this.authenticationService.getCurrentUserIdAndName()
  ): Observable<IExtendedWorkOrder> {
    const userProfile = this.authenticationService.currentUserProfile$.getValue();
    const body = {
      id: extendedWorkOrder.id,
      number: extendedWorkOrder.number,
      siteId: extendedWorkOrder.siteId,
      dueDate: extendedWorkOrder.dueDate,
      status: Number(extendedWorkOrder.status),
      assignedToUser: extendedWorkOrder.assignedToUser,
      createdTimestampUtc: extendedWorkOrder.createdTimestampUtc,
      lastUpdatedTimestampUtc: extendedWorkOrder.lastUpdatedTimestampUtc,
      title: extendedWorkOrder.title,
      description: extendedWorkOrder.description,
      reportedOn: extendedWorkOrder.reportedOn,
      priorityId: extendedWorkOrder.priority,
      categoryId: extendedWorkOrder.category.id,
      serviceDetails: extendedWorkOrder.serviceDetails,
      workOrderAssets: extendedWorkOrder.workOrderAssets,
      workOrderCheckLists: extendedWorkOrder.workOrderChecklists,
      UpdatedBy: userProfile
    };
    return this.httpClient.put<IExtendedWorkOrder>(
      `${this.workOrderURL}/${workOrderGuId}/${userId}`,
      body
    );
  }

  public viewWorkOrderServiceDetailForId(
    workOrderNumber: string,
    siteId = this.currentUserProfile.primarySite.id
  ) {
    return this.httpClient.get<IWorkOrderServiceDetail[]>(
      `${this.workOrderURL}/servicedetail/${siteId}/${workOrderNumber}`
    );
  }

  public updateWorkOrderService(
    workOrderServiceDetail: IWorkOrderServiceDetail
  ) {
    return this.httpClient.patch<IWorkOrderServiceDetail[]>(
      `${this.workOrderURL}/servicedetail/${workOrderServiceDetail.id}`,
      workOrderServiceDetail
    );
  }
  public getWorkOrderSpares(
    workOrderNumber: string,
    siteId = this.currentUserProfile.primarySite.id
  ): Observable<IWorkOrderSpares> {
    const getSpareUrl = `${this.workOrderURL}/spares/Site/${siteId}`;
    let params = new HttpParams();
    params = params.append('workOrderNumber', workOrderNumber);
    return this.httpClient.get<IWorkOrderSpares>(`${getSpareUrl}`, { params });
  }

  public updateWorkOrderSpares(
    workOrderSpares: IWorkOrderSpares
  ): Observable<IWorkOrderSpares> {
    const userId = this.authenticationService.getCurrentUserIdAndName();
    const getSpareUrl = `${this.workOrderURL}/spares/${userId}`;
    return this.httpClient.post<IWorkOrderSpares>(
      `${getSpareUrl}`,
      workOrderSpares
    );
  }
  public listWorkOrderCategory() {
    return this.httpClient.get(`${this.workOrderURL}/categories`);
  }

  public createWorkOrder(args: {
    assignedToUser?: string;
    title: string;
    description: string;
    priorityId: number;
    categoryId: number;
    reportedOn: string;
    dueDate: string;
    workOrderAssets?: string[];
    workOrderChecklists?: string[];
    createdBy?: string;
    siteId?: string;
  }) {
    args.createdBy = this.authenticationService.getCurrentUserIdAndName();
    args.siteId = this.currentUserProfile.primarySite.id;

    const body = args;

    return this.httpClient.post<IExtendedWorkOrder>(
      `${this.workOrderURL}`,
      body
    );
  }

  public getWorkorderCSV(
    siteId: string = this.currentUserProfile.primarySite.id
  ): Observable<any> {
    const options = {
      responseType: 'text' as 'text'
    };
    return this.httpClient.get(
      `${this.workOrderURL}/exportcsv/${siteId}`,
      options
    );
  }
}
