import {
  Component,
  ElementRef,
  HostListener,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  OnInit
} from '@angular/core';
import { AuthenticationService, IUserProfile } from './../../../services';

@Component({
  selector: 'app-user-flyout',
  templateUrl: './user-flyout.component.html',
  styleUrls: ['./user-flyout.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class UserFlyoutComponent implements OnInit {
  public userFlyoutOpen = false;
  public isSignedIn = false;
  public canManageUsers: boolean;
  public userProfile: IUserProfile;
  public isConfirmingSignOut = false;

  constructor(
    private elementRef: ElementRef,
    private changeDetectorRef: ChangeDetectorRef,
    private authentication: AuthenticationService
  ) {
    this.authentication.user.subscribe(data => {
      this.isSignedIn = data ? true : false;
      this.userProfile = data;
      this.changeDetectorRef.markForCheck();
    });
  }

  public ngOnInit() {}

  public handleConfirmSignOut(isConfirmed: boolean) {
    this.isConfirmingSignOut = false;
    if (isConfirmed) {
      this.authentication.logout();
    }
  }

  public closeFlyout(): void {
    this.userFlyoutOpen = false;
  }

  /**
   * Toggle the userFlyout state
   */
  public onUserFlyoutClick() {
    this.userFlyoutOpen = !this.userFlyoutOpen;
  }

  /**
   * Close the flyout when a click occurs outside of it
   */
  @HostListener('document:click', ['$event'])
  public onDocumentClick(e) {
    if (!this.elementRef.nativeElement.contains(e.target)) {
      this.userFlyoutOpen = false;
    }
  }
}
