import { IAssets } from '../asset/asset.types';
import {
  CheckListStatus,
  ICheckList,
  IExtendedTask
} from '../check-lists/check-lists.types';
import { ISkuStock } from '../sku/sku.types';

export interface IWorkorder {
  id: string;
  number: number;
  dueDate: string;
  status: number;
  assignedToUser: string;
  createdTimestampUtc: string;
  lastUpdatedTimestampUtc: string;
  title?: string;
}
export class Workorder {
  public id: string;
  public number: number;
  public title?: string;
  public dueDate: string;
  public status: number;
  public assignedToUser: string;
  public createdTimestampUtc: string;
  public lastUpdatedTimestampUtc: string;

  constructor(workorder?: IWorkorder) {
    if (workorder) {
      this.id = workorder.id;
      this.number = workorder.number;
      this.title = workorder.title;
      this.dueDate = workorder.dueDate;
      this.status = workorder.status;
      this.assignedToUser = workorder.assignedToUser;
      this.createdTimestampUtc = workorder.createdTimestampUtc;
      this.lastUpdatedTimestampUtc = workorder.lastUpdatedTimestampUtc;
    }
  }
}
export interface ISpares {
  sku: ISkuStock;
  quantity: number;
}
export interface IWorkOrderSpares {
  siteId: string;
  workOrderNumber: number;
  spareSkus: ISpares[];
}
export interface IExtendedWorkOrder extends IWorkorder {
  siteId: string;
  workOrderNumber?: string;
  description?: string;
  reportedOn?: string;
  priority?: number;
  category?: IWorkOrderCategory;
  workOrderAssets?: IAssets[];
  attachedFiles?: string[];
  serviceDetails?: string;
  workOrderChecklists?: WorkOrderCheckList[];
}

export enum WorkorderStatus {
  'Open' = 1,
  'In-progress' = 2,
  'Complete' = 3,
  'Closed' = 4
}

export interface IWorkOrderServiceDetail {
  id: string;
  workOrderId: string;
  serviceDetail: string;
  userId: string;
  createdTimestampUtc: string;
}

export enum WorkOrderPriority {
  Critical = 1,
  Major = 2,
  Minor = 3
}
export interface IWorkOrderCategory {
  id: number;
  name: string;
}

export class WorkOrderCheckList {
  public id: string;
  public number: number;
  public numberWithPrefix?: string;
  public siteId?: string;
  public name: string;
  public task?: IExtendedTask[];
  public status?: CheckListStatus;

  constructor(checkList?: ICheckList, task?: IExtendedTask[]) {
    if (checkList) {
      this.id = checkList.id;
      this.number = checkList.number;
      this.numberWithPrefix = checkList.numberWithPrefix;
      this.name = checkList.name;
      this.task = task;
      this.status = checkList.status;
    }
  }
}
