import { Injectable } from '@angular/core';
import {
  CanActivate,
  ActivatedRouteSnapshot,
  RouterStateSnapshot,
  Router
} from '@angular/router';
import { Observable, of } from 'rxjs';
import { mergeMap } from 'rxjs/operators';
import {
  AuthenticationService,
  IRequiredPermissions,
  Toast,
  ToastService,
  ToastType
} from '../services';

@Injectable({
  providedIn: 'root'
})
export class PermissionGuard implements CanActivate {
  constructor(
    private toastService: ToastService,
    private router: Router,
    private authenticationService: AuthenticationService
  ) {}

  public canActivate(
    route: ActivatedRouteSnapshot,
    state: RouterStateSnapshot
  ): Observable<boolean> {
    const requiredPermission: IRequiredPermissions =
      route.data.permissionsRequired;

    return this.authenticationService
      .currentUserHasPermission(requiredPermission)
      .pipe(
        mergeMap(hasPermission => {
          if (hasPermission) {
            return of(true);
          }
          this.toastService.showToast(
            new Toast('Access denied', ToastType.Danger)
          );
          this.router.navigate(['dashboard']);
          return of(false);
        })
      );
  }
}
