import words from 'lodash-es/words';
import { IUserProfile } from './user';

export enum AssetsPermissions {
  None = 0,
  CanViewAssetsList = 1 << 0,
  CanViewAssetDetails = 1 << 1,
  CanModifyAssetDetails = 1 << 2
}

export enum SparesPermissions {
  None = 0,
  CanViewStockList = 1 << 0,
  CanViewBasket = 1 << 1,
  CanAddToBasket = 1 << 2,
  CanModifyBasket = 1 << 3,
  CanViewOrderHistory = 1 << 4,
  CanPlaceOrder = 1 << 5,
  CanManageOrder = 1 << 6,
  CanTransferStock = 1 << 7
}

export enum WorkOrdersPermissions {
  None = 0,
  CanViewWorkOrders = 1 << 0,
  CanViewOrderHistory = 1 << 1,
  CanEditWorkOrder = 1 << 2,
  CanCreateWorkOrder = 1 << 3
}

export enum PreventiveMaintenancePermissions {
  None = 0,
  CanViewPreventiveMaintenance = 1 << 0,
  CanEditPreventiveMaintenance = 1 << 1,
  CanCreatePreventiveMaintenance = 1 << 2
}

export enum CheckListPermissions {
  None = 0,
  CanViewCheckList = 1 << 0,
  CanEditCheckList = 1 << 1,
  CanCreateCheckList = 1 << 2
}

export enum PermissionGroup {
  Assets = 'assetsPermissions',
  Spares = 'sparesPermissions',
  WorkOrders = 'workOrdersPermissions',
  PreventiveMaintenance = 'preventiveMaintenancePermissions',
  CheckList = 'checkListPermissions'
}

export interface IPermissionSet {
  [PermissionGroup.Assets]: number;
  [PermissionGroup.Spares]: number;
  [PermissionGroup.WorkOrders]: number;
  [PermissionGroup.PreventiveMaintenance]: number;
  [PermissionGroup.CheckList]: number;
}

export interface IRequiredPermissions {
  [permissionGroup: string]:
    | AssetsPermissions[]
    | SparesPermissions[]
    | WorkOrdersPermissions[]
    | PreventiveMaintenancePermissions[]
    | CheckListPermissions[];
}

export const permissionGroupToPermissionSet = {
  [PermissionGroup.Assets]: AssetsPermissions,
  [PermissionGroup.Spares]: SparesPermissions,
  [PermissionGroup.WorkOrders]: WorkOrdersPermissions,
  [PermissionGroup.PreventiveMaintenance]: PreventiveMaintenancePermissions,
  [PermissionGroup.CheckList]: CheckListPermissions
};

export const emptyPermissionSet: IPermissionSet = {
  [PermissionGroup.Assets]: 0,
  [PermissionGroup.Spares]: 0,
  [PermissionGroup.WorkOrders]: 0,
  [PermissionGroup.PreventiveMaintenance]: 0,
  [PermissionGroup.CheckList]: 0
};

export enum Role {
  None = 'NA',
  Manager = 'upkeep_manager',
  Technician = 'upkeep_technician',
  Reviewer = 'upkeep_reviewer'
}

export function roleString(role: Role): string {
  const roleWords: string[] = words(Role[role]);
  return roleWords.slice(1).join(' ');
}

export function hasPermission(permission, permissionSubSet) {
  return (permissionSubSet & permission) === permission;
}

export function profileHasPermission(
  user: IUserProfile,
  requiredPermissions: IRequiredPermissions
) {
  const userPermissionSet = {
    assetsPermissions:
      user.userRole.permission.findIndex(
        module => module.moduleName === 'Assets'
      ) > -1
        ? user.userRole.permission[
            user.userRole.permission.findIndex(
              module => module.moduleName === 'Assets'
            )
          ].permissionValue
        : 0,
    sparesPermissions:
      user.userRole.permission.findIndex(
        module => module.moduleName === 'Spares'
      ) > -1
        ? user.userRole.permission[
            user.userRole.permission.findIndex(
              module => module.moduleName === 'Spares'
            )
          ].permissionValue
        : 0,
    workOrdersPermissions:
      user.userRole.permission.findIndex(
        module => module.moduleName === 'WorkOrders'
      ) > -1
        ? user.userRole.permission[
            user.userRole.permission.findIndex(
              module => module.moduleName === 'WorkOrders'
            )
          ].permissionValue
        : 0,
    preventiveMaintenancePermissions:
      user.userRole.permission.findIndex(
        module => module.moduleName === 'PreventiveMaintenance'
      ) > -1
        ? user.userRole.permission[
            user.userRole.permission.findIndex(
              module => module.moduleName === 'PreventiveMaintenance'
            )
          ].permissionValue
        : 0,
    checkListPermissions:
      user.userRole.permission.findIndex(
        module => module.moduleName === 'CheckList'
      ) > -1
        ? user.userRole.permission[
            user.userRole.permission.findIndex(
              module => module.moduleName === 'CheckList'
            )
          ].permissionValue
        : 0
  };
  for (const permissionGroup in requiredPermissions) {
    for (const permission of requiredPermissions[permissionGroup]) {
      if (!hasPermission(permission, userPermissionSet[permissionGroup])) {
        return false;
      }
    }
  }
  return true;
}
