import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { DashboardComponent } from './dashboard';
import { AuthGuard } from './guards';
import { DependencyResolver } from './DependencyResolver';
import {
  AssetsPermissions,
  IRequiredPermissions,
  PermissionGroup,
  WorkOrdersPermissions,
  PreventiveMaintenancePermissions,
  CheckListPermissions
} from './services';
import { PermissionGuard } from './guards/permission.guard';

const accountModule = () =>
  import('./user-account/account.module').then(x => x.AccountModule);

export const assetsRequiredPermissions: IRequiredPermissions = {
  [PermissionGroup.Assets]: [AssetsPermissions.CanViewAssetsList]
};

export const workordersRequiredPermissions: IRequiredPermissions = {
  [PermissionGroup.WorkOrders]: [WorkOrdersPermissions.CanViewWorkOrders]
};

export const preventiveMaintenancePermissions: IRequiredPermissions = {
  [PermissionGroup.PreventiveMaintenance]: [
    PreventiveMaintenancePermissions.CanViewPreventiveMaintenance
  ]
};

export const checkListPermissions: IRequiredPermissions = {
  [PermissionGroup.CheckList]: [CheckListPermissions.CanViewCheckList]
};

export const routes: Routes = [
  { path: '', component: DashboardComponent, canActivate: [AuthGuard] },
  { path: 'account', loadChildren: accountModule },
  {
    path: 'dashboard',
    component: DashboardComponent,
    canActivate: [AuthGuard]
  },
  {
    path: 'assets',
    canActivate: [AuthGuard, PermissionGuard],
    loadChildren: () =>
      import('./assets/assets.module').then(m => m.AssetsModule),
    resolve: {
      dependenciesResolved: DependencyResolver
    },
    data: {
      permissionsRequired: assetsRequiredPermissions
    }
  },
  {
    path: 'spares',
    canActivate: [AuthGuard],
    loadChildren: () =>
      import('./spares/spares.module').then(m => m.SparesModule),
    resolve: {
      dependenciesResolved: DependencyResolver
    }
  },
  {
    path: 'work-orders',
    canActivate: [AuthGuard, PermissionGuard],
    loadChildren: () =>
      import('./work-orders/work-orders.module').then(m => m.WorkOrdersModule),
    resolve: {
      dependenciesResolved: DependencyResolver
    },
    data: {
      permissionsRequired: workordersRequiredPermissions
    }
  },
  {
    path: 'preventive-maintenance',
    canActivate: [AuthGuard, PermissionGuard],
    loadChildren: () =>
      import('./preventive-maintenance/preventive-maintenance.module').then(
        m => m.PreventiveMaintenanceModule
      ),
    resolve: {
      dependenciesResolved: DependencyResolver
    },
    data: {
      permissionsRequired: preventiveMaintenancePermissions
    }
  },
  {
    path: 'check-list',
    canActivate: [AuthGuard, PermissionGuard],
    loadChildren: () =>
      import('./check-lists/check-lists.module').then(m => m.CheckListsModule),
    resolve: {
      dependenciesResolved: DependencyResolver
    },
    data: {
      permissionsRequired: checkListPermissions
    }
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule],
  providers: []
})
export class AppRoutingModule {}
