export enum CheckListStatus {
  'Active' = 1,
  'Obsolete' = 2
}

export interface ITask {
  id?: string;
  sequenceNumber?: number;
  description: string;
  isDeleted?: boolean;
}

export interface ICheckList {
  id: string;
  number: number;
  numberWithPrefix?: string;
  siteId?: string;
  name: string;
  status?: CheckListStatus;
}

export class CheckList {
  public id: string;
  public number: number;
  public numberWithPrefix?: string;
  public siteId?: string;
  public name: string;
  public task?: ITask[];
  public status?: CheckListStatus;

  constructor(checkList?: ICheckList, task?: ITask[]) {
    if (checkList) {
      this.id = checkList.id;
      this.number = checkList.number;
      this.numberWithPrefix = checkList.numberWithPrefix;
      this.name = checkList.name;
      this.task = task;
      this.status = checkList.status;
    }
  }
}

export interface IExtendedTask extends ITask {
  isCompleted: boolean;
}
