import {
  NavigationService,
  NavItem
} from '../../../services/navigation/navigation.service';
import { Component, OnInit } from '@angular/core';
import { filter, take } from 'rxjs/operators';

@Component({
  selector: 'app-nav',
  templateUrl: './nav.component.html',
  styleUrls: ['./nav.component.scss']
})
export class NavComponent implements OnInit {
  public navExpanded: Boolean;
  public navItems: NavItem[];

  constructor(public navigationService: NavigationService) {}

  public ngOnInit() {
    this.navigationService
      .getNavigationTree()
      .pipe(
        filter(x => x.length > 0),
        take(1)
      )
      .subscribe(navItems => {
        this.navItems = navItems;
      });
    this.navigationService.navigationExpanded.subscribe(
      navExpanded => (this.navExpanded = navExpanded)
    );
  }

  public handleHamburgerClick() {
    this.navigationService.toggleNavigation();
  }

  public handleNavItemClick() {
    if (window.innerWidth <= 768 && this.navExpanded) {
      this.navigationService.toggleNavigation();
    }
  }
}
