export interface IBasketItem {
  createdByUserId: string;
  createdByUserName: string;
  skuId: string;
  skuDescription: string;
  uom: string;
  qty: number;
}

export class BasketItem implements IBasketItem {
  public createdByUserId: string;
  public createdByUserName: string;
  public skuId: string;
  public skuDescription: string;
  public uom: string;
  public qty: number;

  constructor(item: IBasketItem) {
    this.createdByUserId = item.createdByUserId;
    this.createdByUserName = item.createdByUserName;
    this.skuId = item.skuId;
    this.skuDescription = item.skuDescription;
    this.uom = item.uom;
    this.qty = item.qty;
  }
}

export interface IBasket {
  siteId: string;
  items: IBasketItem[];
  lastUpdatedTimestampUtc: string;
}

export class Basket implements IBasket {
  siteId: string;
  items: IBasketItem[];
  lastUpdatedTimestampUtc: string;

  constructor(basket: IBasket) {
    this.siteId = basket.siteId;
    this.items = basket.items.map(item => new BasketItem(item));
    this.lastUpdatedTimestampUtc = basket.lastUpdatedTimestampUtc;
  }
}
