import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class SearchFilterService {
  constructor() {}

  public searchWOTextValue: string = '';
  public searchPMTextValue: string = '';
  public searchPMFilterValue: boolean = false;
  public searchWOTextValueCache: string = '';
  public searchPMTextValueCache: string = '';
  public searchPMFilterValueCache: boolean = false;
  public searchCLFilterValue: boolean = false;
  public searchCLTextValue: string = '';
  public searchCLFilterValueCache: boolean = false;
  public searchCLTextValueCache: string = '';

  public initiateWOSearchFilter() {
    this.searchWOTextValue = '';
  }

  public assignWOSearchFilterCache() {
    this.searchWOTextValue = this.searchWOTextValueCache;
  }

  public initiatePMSearchFilter() {
    this.searchPMTextValue = '';
    this.searchPMFilterValue = false;
  }

  public clearWOFilter() {
    this.searchWOTextValueCache = '';
  }

  public assignPMSearchFilterCache() {
    this.searchPMTextValue = this.searchPMTextValueCache;
    this.searchPMFilterValue = this.searchPMFilterValueCache;
  }

  public assignCLSearchFilterCache() {
    this.searchCLTextValue = this.searchCLTextValueCache;
    this.searchCLFilterValue = this.searchCLFilterValueCache;
  }

  public clearCLFilter() {
    this.searchCLTextValue = '';
    this.searchCLFilterValue = false;
  }

  public clearPMFilter() {
    this.searchPMTextValueCache = '';
    this.searchPMFilterValueCache = false;
  }
}
