import { Component, OnInit } from '@angular/core';
import {
  AuthenticationService,
  PermissionGroup,
  SparesPermissions
} from 'src/app/services';

@Component({
  selector: 'app-function-bar',
  templateUrl: './function-bar.component.html',
  styleUrls: ['./function-bar.component.scss']
})
export class FunctionBarComponent implements OnInit {
  public canViewBasket: boolean;
  constructor(private authenticationService: AuthenticationService) {}

  public ngOnInit() {
    this.authenticationService
      .currentUserHasPermission({
        [PermissionGroup.Spares]: [SparesPermissions.CanViewBasket]
      })
      .subscribe(hasPermission => {
        this.canViewBasket = hasPermission;
      });
  }
}
