import { Site } from '../';

export interface IModulePersmission {
  moduleName: string;
  permissionValue: number;
}

export interface IUserProfile {
  name: string;
  userId?: string;
  givenName?: string;
  displayName?: string;
  surName?: string;
  lastLogon?: string;
  userRole?: {
    name: string;
    groupName: string;
    permission: IModulePersmission[];
  };
  commonCodeIdentifier?: string;
  primarySite?: Site;
  sites?: Site[];
  emailAddress?: string;
}
export class UserProfile implements IUserProfile {
  public name: string;
  public userRole?: {
    name: string;
    groupName: string;
    permission: IModulePersmission[];
  };
  public userId?: string;
  public password?: string;
  public givenName?: string;
  public surName?: string;
  public lastLogon?: string;
  public displayName?: string;
  public commonCodeIdentifier?: string;
  public primarySite?: Site;
  public sites?: Site[];
  public emailAddress?: string;

  constructor(userProfile: IUserProfile) {
    this.name = userProfile.name;
    this.userRole = userProfile.userRole;
    this.userId = userProfile.userId;
    this.givenName = userProfile.givenName;
    this.surName = userProfile.surName;
    this.displayName = userProfile.displayName;
    this.lastLogon = userProfile.lastLogon;
    this.commonCodeIdentifier = userProfile.commonCodeIdentifier;
    this.primarySite = userProfile.primarySite;
    this.sites = userProfile.sites;
    this.emailAddress = userProfile.emailAddress;
  }
}

export class User {
  userId: string;
  username: string;
  password: string;
  firstName: string;
  lastName: string;
  token: string;
}
