import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable, Subscription, of } from 'rxjs';
import { AuthenticationService } from '../authentication/authentication.service';
import { HttpClient } from '@angular/common/http';
import { Basket, IBasket, IBasketItem } from './basket.types';
import { UserProfile } from '../user-account/user';
import {
  PermissionGroup,
  SparesPermissions,
  profileHasPermission
} from '../user-account/permissions';
import { environment } from 'src/environments/environment';
import { safelyUnsubscribeFrom } from 'src/utils/observableUtils';
import { mergeMap } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class BasketService {
  public basket$: BehaviorSubject<Basket> = new BehaviorSubject(null);
  public basketUrl$: BehaviorSubject<string> = new BehaviorSubject(null);
  public userProfileSub: Subscription;
  public userId: string;
  constructor(
    private authenticationService: AuthenticationService,
    private httpClient: HttpClient
  ) {
    this.userProfileSub = this.authenticationService.currentUserProfile$.subscribe(
      (userProfile: UserProfile) => {
        const canViewBasket = profileHasPermission(userProfile, {
          [PermissionGroup.Spares]: [SparesPermissions.CanViewBasket]
        });
        if (
          userProfile &&
          userProfile.primarySite &&
          userProfile.primarySite.id &&
          canViewBasket &&
          this.basketUrl$.getValue() === null
        ) {
          this.userId = userProfile.userId;
          const siteId = userProfile.primarySite.id;
          this.basketUrl$.next(
            `${environment.apiBaseUrl}/order-basket/${siteId}`
          );
          safelyUnsubscribeFrom(this.userProfileSub);
        }
      }
    );

    this.getBasket().subscribe((data: IBasket) => {
      if (data) {
        this.basket$.next(new Basket(data));
      }
    });
  }

  public getBasket(): Observable<IBasket> {
    return this.basketUrl$.pipe(
      mergeMap((basketUrl: string) => {
        if (basketUrl) {
          return this.httpClient.get<IBasket>(basketUrl);
        }
        return of(null);
      })
    );
  }

  public addToBasket(
    basketItem: IBasketItem,
    siteId: string
  ): Observable<boolean> {
    const basketUrl = `${environment.apiBaseUrl}/order-basket`;
    const body = {
      siteId: siteId,
      createdByUserId: basketItem.createdByUserId,
      createdByUserName: basketItem.createdByUserName,
      skuId: basketItem.skuId,
      skuDescription: basketItem.skuDescription,
      uom: basketItem.uom,
      qty: basketItem.qty
    };
    return this.httpClient.post<IBasket>(basketUrl, body).pipe(
      mergeMap((iBasket: IBasket) => {
        const basket = new Basket(iBasket);
        this.basket$.next(basket);
        return of(true);
      })
    );
  }

  public updateBasketItem(skuId: string, qty: number): Observable<boolean> {
    const basketUrl = this.basketUrl$.getValue();
    return this.httpClient
      .patch<IBasket>(
        `${basketUrl}?skuId=${skuId}&userId=${this.userId}&qty=${qty}`,
        {}
      )
      .pipe(
        mergeMap((iBasket: IBasket) => {
          const basket = new Basket(iBasket);
          this.basket$.next(basket);
          return of(true);
        })
      );
  }

  public deleteBasketItem(
    skuId: string,
    createdByUserId: string,
    deletedByUserId: string
  ): Observable<boolean> {
    const basketUrl = this.basketUrl$.getValue();
    return this.httpClient
      .delete<IBasket>(
        `${basketUrl}?skuId=${skuId}&createdByUserId=${createdByUserId}&deletedByUserId=${deletedByUserId}`
      )
      .pipe(
        mergeMap((iBasket: IBasket) => {
          const basket = new Basket(iBasket);
          this.basket$.next(basket);
          return of(true);
        })
      );
  }
}
