import { HttpClient, HttpParams } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { ISkuStock, SparesGroup } from './sku.types';
import { AuthenticationService } from '../authentication/authentication.service';

@Injectable({
  providedIn: 'root'
})
export class SkuService {
  public skuStockItemUrl = `${environment.apiBaseUrl}/stock`;
  public sparesGroupUrl = `${environment.apiBaseUrl}/stock/sparegroup`;

  constructor(
    private http: HttpClient,
    private authenticationService: AuthenticationService
  ) {}

  public get primarySiteId(): string {
    const userProfile = this.authenticationService.currentUserProfile$.getValue();
    if (userProfile && userProfile.primarySite) {
      return userProfile.primarySite.id;
    }
    return null;
  }

  public getSkuStockForSparesGroup(
    sparesGroupId: string,
    sparesSubGroupId: string,
    take: Number,
    offset: Number,
    siteId: string,
    text?: string
  ): Observable<ISkuStock[]> {
    let params = new HttpParams();
    params = params
      .append('GroupId', sparesGroupId)
      .append('SubGroupId', sparesSubGroupId)
      .append('take', take.toString())
      .append('offset', offset.toString())
      .append('siteId', siteId);

    if (text) {
      params = params.append('text', text);
    }
    return this.http.get<ISkuStock[]>(`${this.skuStockItemUrl}`, {
      params
    });
  }

  public getSkuStockForSpares(
    sparesGroups: string,
    text?: string,
    siteId?: string
  ): Observable<ISkuStock[]> {
    siteId =
      siteId ||
      this.authenticationService.currentUserProfile$.value.primarySite.id;
    let params = new HttpParams();
    if (sparesGroups !== '') {
      const groups = sparesGroups.split('~', 2);
      params = params.append('GroupId', groups[0]);
      params = params.append('SubGroupId', groups[1]);
    }
    params = params.append('siteId', siteId);
    params = params.append('text', text);

    return this.http.get<ISkuStock[]>(`${this.skuStockItemUrl}`, {
      params
    });
  }

  public getSparesGroups(
    text?: string,
    siteId?: string
  ): Observable<SparesGroup[]> {
    siteId =
      siteId ||
      this.authenticationService.currentUserProfile$.value.primarySite.id;

    let params = new HttpParams();
    params = params.append('siteId', siteId);
    if (text) {
      params = params.append('text', text);
    }
    return this.http.get<SparesGroup[]>(`${this.sparesGroupUrl}/`, {
      params
    });
  }

  public getSkuStockForSkuId(
    skuId: string,
    siteId: string = this.primarySiteId
  ): Observable<ISkuStock[]> {
    let params = new HttpParams();
    params = params.append('text', skuId.toString()).append('siteId', siteId);
    return this.http.get<ISkuStock[]>(`${this.skuStockItemUrl}`, {
      params
    });
  }
}
