import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import {
  HttpClient,
  HttpErrorResponse,
  HttpHeaders
} from '@angular/common/http';
import { BehaviorSubject, Observable, of, throwError } from 'rxjs';
import { catchError, filter, map, mergeMap } from 'rxjs/operators';
import { environment } from './../../../environments/environment';
import { IUserProfile, UserProfile } from './../user-account/user';
import {
  IRequiredPermissions,
  profileHasPermission,
  Role
} from '../user-account/permissions';

@Injectable({ providedIn: 'root' })
export class AuthenticationService {
  public currentUserProfile$: BehaviorSubject<UserProfile>;
  public user: Observable<UserProfile>;

  constructor(private router: Router, private http: HttpClient) {
    this.currentUserProfile$ = new BehaviorSubject<UserProfile>(
      JSON.parse(localStorage.getItem('user'))
    );
    this.user = this.currentUserProfile$.asObservable();
  }

  public get userValue(): UserProfile {
    return this.currentUserProfile$.value;
  }

  public login(userid, password) {
    const body = {
      userId: userid,
      password: password
    };
    const Api_Key =
      '418413972367e5976207eeb05530ec52213d3b0a0bfcb55001fa00eb0f1c4225';
    let header = new HttpHeaders().set('content-type', 'application/json');
    header = header.append('Api_Key', Api_Key);
    return this.http
      .post<IUserProfile>(`${environment.apiBaseUrl}/userauthorization`, body, {
        headers: header
      })
      .pipe(
        map(userProfile => {
          localStorage.setItem('user', JSON.stringify(userProfile));
          this.currentUserProfile$.next(userProfile);
          return userProfile;
        }),
        catchError(this.handleError)
      );
  }

  public currentUserHasPermission(
    requiredPermissions: IRequiredPermissions
  ): Observable<boolean> {
    return this.currentUserProfile$.pipe(
      filter(userProfile => userProfile !== null),
      mergeMap(userProfile => {
        if (!profileHasPermission(userProfile, requiredPermissions)) {
          return of(false);
        }
        return of(true);
      })
    );
  }

  public currentUserIsManager(): Observable<boolean> {
    return this.currentUserProfile$.pipe(
      filter(userProfile => userProfile !== null),
      mergeMap(userProfile => {
        if (userProfile.userRole.groupName !== Role.Manager) {
          return of(false);
        }
        return of(true);
      })
    );
  }

  private handleError(error: HttpErrorResponse) {
    if (error.error instanceof ErrorEvent) {
      console.error('An error occurred:', error.error.message);
    } else {
      console.error(
        `Backend returned code ${error.status}, ` +
          `body was: ${error.error.message}`
      );
    }
    return throwError('Something bad happened; please try again later.');
  }

  public logout() {
    localStorage.removeItem('user');
    this.currentUserProfile$.next(null);
    this.router.navigate(['/account/login']);
  }

  public getCurrentUserIdAndName(): string {
    const userProfile: IUserProfile = this.currentUserProfile$.getValue();

    if (userProfile) {
      return userProfile.userId
        .concat('-', userProfile.displayName)
        .substring(0, 50);
    } else {
      return '';
    }
  }
}
