import { Injectable } from '@angular/core';
import { Resolve, ActivatedRouteSnapshot } from '@angular/router';
import { AuthenticationService } from './services';

@Injectable({
  providedIn: 'root'
})
export class DependencyResolver implements Resolve<boolean> {
  constructor(private userProfileService: AuthenticationService) {}

  public resolve(route: ActivatedRouteSnapshot, state): Promise<boolean> {
    return new Promise<boolean>(resolve => {
      const interval = setInterval(_ => {
        if (this.userProfileService.currentUserProfile$.value !== null) {
          clearInterval(interval);
          return resolve(true);
        }
      }, 100);
    });
  }
}
