import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { BasketService } from 'src/app/services';
import { Basket } from 'src/app/services/basket/basket.types';

@Component({
  selector: 'app-basket',
  templateUrl: './basket.component.html',
  styleUrls: ['./basket.component.scss']
})
export class BasketComponent implements OnInit {
  public basketLength = 0;

  constructor(
    private basketService: BasketService,
    private cdRef: ChangeDetectorRef
  ) {
    this.basketService.basket$.subscribe((basket: Basket) => {
      if (basket && basket.items) {
        this.basketLength = basket.items.length;
        this.cdRef.markForCheck();
      }
    });
  }

  ngOnInit(): void {}
}
