import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable, of } from 'rxjs';
import { mergeMap } from 'rxjs/operators';
import { IUserProfile } from '../user-account/user';
import {
  AssetsPermissions,
  CheckListPermissions,
  IRequiredPermissions,
  PermissionGroup,
  PreventiveMaintenancePermissions,
  profileHasPermission,
  SparesPermissions,
  WorkOrdersPermissions
} from '..';
import { AuthenticationService } from '..';

// TODO: Possibly refactor interface into a class with getters for the conventional fields
export interface NavItem {
  label: string;
  route?: string;
  slug?: string;
  icon?: string;
  expanded?: boolean;
  showOnDash?: boolean;
  dashPanelColour?: string;
  dashIllustration?: string;
  isLandingPage?: boolean;
  landingPageDescription?: string;
  routePrefix?: string;
  children?: NavItem[];
  toggleExpanded?: Function;
  permissionsRequired?: IRequiredPermissions;
  hidden?: boolean;
  shortLabel?: string;
}

@Injectable({
  providedIn: 'root'
})
export class NavigationService {
  public navigationExpanded = new BehaviorSubject<boolean>(false);
  public navigationExpanded$ = this.navigationExpanded.asObservable();
  private generatedNavTreeCache: NavItem[];
  private generatedNavTreeCacheWithHiddenItems: NavItem[];

  private navigationTree: NavItem[] = [
    {
      label: 'Work Orders',
      showOnDash: true,
      dashPanelColour: '#EFEEDA',
      permissionsRequired: {
        [PermissionGroup.WorkOrders]: [WorkOrdersPermissions.CanViewWorkOrders]
      }
    },
    {
      label: 'Preventive Maintenance',
      showOnDash: true,
      dashPanelColour: '#D6E5D8',
      permissionsRequired: {
        [PermissionGroup.PreventiveMaintenance]: [
          PreventiveMaintenancePermissions.CanViewPreventiveMaintenance
        ]
      }
    },
    {
      label: 'Assets',
      route: './assets',
      showOnDash: false,
      dashPanelColour: '#D6E5D8',
      permissionsRequired: {
        [PermissionGroup.Assets]: [AssetsPermissions.CanViewAssetsList]
      }
    },
    {
      label: 'Spares',
      isLandingPage: true,
      landingPageDescription: `
      Within this module you can perform many functions related to spares stock, ranging from
      something as simple as viewing a list of your stock, right up to ordering spare
      to a purchase or stores.
      `,
      children: [
        {
          label: 'Stock List',
          showOnDash: true,
          dashPanelColour: '#D4DADD',
          permissionsRequired: {
            [PermissionGroup.Spares]: [SparesPermissions.CanViewStockList]
          }
        },
        {
          label: 'Transfer Stock',
          showOnDash: false,
          dashPanelColour: '#D4DADD',
          permissionsRequired: {
            [PermissionGroup.Spares]: [SparesPermissions.CanViewStockList]
          }
        },
        {
          label: 'Order History',
          showOnDash: false,
          dashPanelColour: '#D4DADD',
          permissionsRequired: {
            [PermissionGroup.Spares]: [SparesPermissions.CanViewOrderHistory]
          }
        }
      ]
    },
    {
      label: 'Check Lists',
      route: './check-list',
      showOnDash: false,
      dashPanelColour: '#D6E5D8',
      permissionsRequired: {
        [PermissionGroup.CheckList]: [CheckListPermissions.CanViewCheckList]
      }
    }
  ];

  constructor(public authenticationService: AuthenticationService) {}

  public toggleNavigation() {
    this.navigationExpanded.next(!this.navigationExpanded.getValue());
  }

  public addFieldsByConvention(navItem, routePrefix = '/') {
    // The slug is a lower case, hyphenated version of the label
    const slug = navItem.label.replace(/\s+/g, '-').toLowerCase();
    const route = navItem.route ? navItem.route : routePrefix + slug;
    return {
      ...navItem,
      route,
      expanded: false,
      toggleExpanded: function() {
        this.expanded = !this.expanded;
      },
      icon: `/assets/img/module-icons/${slug}.svg`,
      ...(navItem.showOnDash && {
        dashIllustration: `/assets/img/module-icons/dash-illustrations/${slug}.svg`
      }),
      ...(navItem.children && {
        children: navItem.children.map(child =>
          this.addFieldsByConvention(child, `${route}/`)
        )
      })
    };
  }

  /**
   * @param roleTemplate The users role template
   * @param additionalPermissions The users additional permissions
   * @param navItem The nav item to check permissions on - recursively also checks children
   * @returns either a navItem with its children appropriately filtered or null - use with map then filter top level nulls afterwards
   */
  private removeItemsBasedOnPermissions(
    user: IUserProfile,
    navItem: NavItem
  ): NavItem {
    if (
      navItem.permissionsRequired &&
      !profileHasPermission(user, navItem.permissionsRequired)
    ) {
      return;
    }

    let children = [];

    if (navItem.children) {
      children = navItem.children
        .map(child => this.removeItemsBasedOnPermissions(user, child))
        .filter(item => item); // Remove undefined items
    }

    if (navItem.children && children.length === 0) {
      return;
    }

    return {
      ...navItem,
      ...(navItem.children && { children })
    };
  }

  public getNavigationTree(includeHiddenItems = false): Observable<NavItem[]> {
    if (this.generatedNavTreeCache && !includeHiddenItems) {
      return of(this.generatedNavTreeCache);
    }

    if (this.generatedNavTreeCacheWithHiddenItems && includeHiddenItems) {
      return of(this.generatedNavTreeCacheWithHiddenItems);
    }

    return this.authenticationService.currentUserProfile$.pipe(
      mergeMap(userProfile => {
        if (!userProfile) {
          return of([]);
        }
        const navTree = this.navigationTree
          .map(navItem =>
            this.removeItemsBasedOnPermissions(userProfile, navItem)
          )
          .filter(item => item) // Remove undefined items
          .filter(item => !item.hidden || (includeHiddenItems && item.hidden))
          .map(navItem => this.addFieldsByConvention(navItem));
        if (includeHiddenItems) {
          this.generatedNavTreeCacheWithHiddenItems = navTree;
        } else {
          this.generatedNavTreeCache = navTree;
        }
        return of(navTree);
      })
    );
  }
}
