import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';

export enum ToastType {
  Success = 'success',
  Danger = 'danger'
}

export class Toast {
  public message: string;
  public type: ToastType;

  constructor(message: string, type = ToastType.Success) {
    this.message = message;
    this.type = type;
  }
}

@Injectable({
  providedIn: 'root'
})
export class ToastService {
  private activeToast = new BehaviorSubject<Toast>(null);
  private toastStack = [];
  public get toastDuration() {
    return 3500;
  }

  public showToast(toast: Toast) {
    const noActiveToast = this.activeToast.getValue() === null;
    const toastStackEmpty = this.toastStack.length === 0;

    if (noActiveToast && toastStackEmpty) {
      this.setNextToast(toast);
    } else {
      this.toastStack.push(toast);
    }
  }

  public showErrorToast(message = 'Sorry something went wrong') {
    this.showToast(new Toast(message, ToastType.Danger));
  }

  private setNextToast(toast: Toast) {
    this.activeToast.next(toast);
    setTimeout(() => {
      // The toast needs to be set to null briefly so that the CSS
      // animation is triggered by the toast being removed and added
      // back to the DOM
      this.activeToast.next(null);
      setTimeout(() => this.loadNextToastFromStack(), 50);
    }, this.toastDuration);
  }

  private loadNextToastFromStack() {
    if (this.toastStack.length > 0) {
      const nextToast = this.toastStack.shift();
      this.setNextToast(nextToast);
    }
  }
}
