import { NavigationService } from '../services/navigation/navigation.service';
import { Component, OnInit, OnDestroy } from '@angular/core';
import { filter, take } from 'rxjs/operators';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.scss']
})
export class DashboardComponent implements OnInit, OnDestroy {
  public dashItems = [];
  private sub: Subscription;

  constructor(private navigationService: NavigationService) {}

  public ngOnInit(): void {
    this.sub = this.navigationService
      .getNavigationTree()
      .pipe(
        filter(x => x.length > 0),
        take(1)
      )
      .subscribe(navItems => {
        this.dashItems = this.populateDashItems(navItems);
      });
  }

  public ngOnDestroy(): void {
    this.sub && !this.sub.closed && this.sub.unsubscribe();
  }

  public populateDashItems(navItems: Array<object>) {
    const dashItems = [];

    const iterateDashItems = navItem => {
      if (navItem.children) {
        navItem.children.map(childItem => iterateDashItems(childItem));
      }

      if (navItem.showOnDash) {
        dashItems.push(navItem);
      }
    };

    navItems.map(navItem => iterateDashItems(navItem));
    return dashItems;
  }
}
