import { Observable } from 'rxjs';
import { CheckList, ITask } from './check-lists.types';
import { Injectable } from '@angular/core';
import { AuthenticationService } from '../authentication/authentication.service';
import { IUserProfile } from '../user-account/user';
import { HttpClient, HttpParams } from '@angular/common/http';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class CheckListsService {
  private checkListUrl = `${environment.apiBaseUrl}/check-list`;
  constructor(
    private http: HttpClient,
    private authenticationService: AuthenticationService
  ) {}

  private get currentUserProfile(): IUserProfile {
    const userProfile = this.authenticationService.currentUserProfile$.getValue();
    if (userProfile && userProfile.primarySite) {
      return userProfile;
    }
    return null;
  }

  public getCheckListsList(
    args: {
      offset: number;
      take: number;
      searchText: string;
      includeObsolete: boolean;
    },
    siteId = this.currentUserProfile.primarySite.id
  ): Observable<CheckList[]> {
    let params = new HttpParams();
    params = params.append('offset', args.offset.toString());
    params = params.append('take', args.take.toString());
    params = params.append('searchText', args.searchText.toString());
    params = params.append('includeObsolete', args.includeObsolete.toString());

    return this.http.get<CheckList[]>(`${this.checkListUrl}/${siteId}?`, {
      params
    });
  }

  public createCheckList(args: {
    name: string;
    siteId?: string;
    task?: ITask[];
  }): Observable<CheckList> {
    args.siteId = this.currentUserProfile.primarySite.id;
    const createdBy: string = this.authenticationService.getCurrentUserIdAndName();
    const body = args;
    return this.http.post<CheckList>(
      `${this.checkListUrl}?createdBy=${createdBy}`,
      body
    );
  }

  public viewCheckListForId(
    checkListId: number,
    siteId = this.currentUserProfile.primarySite.id
  ) {
    return this.http.get<CheckList>(
      `${this.checkListUrl}/${siteId}/${checkListId}`
    );
  }

  public updateCheckListDetailForId(
    extendedCheckList: CheckList,
    userId = this.authenticationService.getCurrentUserIdAndName()
  ): Observable<CheckList> {
    return this.http.put<CheckList>(
      `${this.checkListUrl}?updatedBy=${userId}`,
      extendedCheckList
    );
  }
}
