import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from 'src/environments/environment';
import { AuthenticationService } from '../authentication/authentication.service';
import { Observable } from 'rxjs';
import { IFilehandler } from './filehandler.type';

@Injectable({
  providedIn: 'root'
})
export class FilehandlerService {
  private filehandlerUrl = `${environment.apiBaseUrl}/filehandler`;

  constructor(
    private httpClient: HttpClient,
    private authenticationService: AuthenticationService
  ) {}

  public get primarySiteId(): string {
    const userProfile = this.authenticationService.currentUserProfile$.getValue();
    if (userProfile && userProfile.primarySite) {
      return userProfile.primarySite.id;
    }
    return null;
  }

  public uploadFiles(
    dataId: number,
    ModuleName: number,
    fileCollection: any,
    siteId = this.primarySiteId
  ): Observable<IFilehandler> {
    const formData = new FormData();
    (fileCollection ? fileCollection : []).forEach(srcFile => {
      formData.append('Attachments', srcFile);
    });
    formData.append('SiteId', siteId);
    formData.append('Id', dataId.toString());
    formData.append('ModuleName', ModuleName.toString());

    return this.httpClient.post<IFilehandler>(
      `${this.filehandlerUrl}/upload`,
      formData
    );
  }

  public getFile(filePath: string): Observable<any> {
    const getFileUrl = `${this.filehandlerUrl}/getfile`;
    let httpHeader = new HttpHeaders();
    httpHeader = httpHeader.append('accept', 'text/plain');
    httpHeader = httpHeader.append('filePath', filePath);

    const options = {
      responseType: 'blob' as 'blob',
      headers: httpHeader
    };
    return this.httpClient.get(getFileUrl, options);
  }

  public deleteFile(filePath: string, isDeleteAllFiles: boolean) {
    const deleteFileUrl = `${
      this.filehandlerUrl
    }/delete?isDeleteAllFiles=${isDeleteAllFiles.toString()}`;
    let httpHeader = new HttpHeaders();
    httpHeader = httpHeader.append('accept', 'text/plain');
    httpHeader = httpHeader.append('path', filePath);

    const options = {
      responseType: 'text' as 'text',
      headers: httpHeader
    };
    return this.httpClient.delete(deleteFileUrl, options);
  }
}
